<template>
    <div>
        <el-button type="text" @click="dialogVisible = true">点击打开 Dialog</el-button>

        <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                width="600px"
                :append-to-body="true">
            <person-tree @getPersons="getPersons"></person-tree>
        </el-dialog>

    </div>
</template>
<script>
    import PersonTree from './personTree'
    export default {
        components: {PersonTree},
        props: ['data'],
        watch: {
            filterText(val) {
                this.$refs.tree.filter(val);
            }
        },

        methods: {
            getPersons(data) {
                console.log(data)
            }
        },

        data() {
            return {
                selectData: [],
                dialogVisible: false,
                filterText: '',
                defaultProps: {
                    children: 'children',
                    label: 'label'
                }
            };
        }
    };
</script>


